import React from 'react';
import PropTypes from 'prop-types';
import { PersonIcon } from '../../components/icons';
import Message from './message';

export default function LastMessagesList({ lastMessages }) {
    return lastMessages.map((message, index) => {
        if (index >3) return
        const key = `message-${index}`;
        const lastItemIndex = (lastMessages.length - 1);
        const hasDivider = (index !== lastItemIndex);
        const {
            avatar,
            userName,
            date,
            content,
            avatarTitle,
        } = message;
        return (
            <Message
                key={key}
                avatarIcon={<PersonIcon />}
                avatarSrc={avatar}
                userName={userName}
                date={date}
                content={'El 01SEP19, se recibi informacin del CZGNB52, notificando comisin integrada por cuatro (4) efectivos de Tropa Profesional adscritos Desur-52, al mando del TTE Vivas Andrs, en labores de Patrullaje de Seguridad Ciudadana, en el sector El Viedo, Parroquia San Cristbal, Municipio Simn Bolvar, Estado Anzotegui, efectuaron la recuperacin de un (01) vehculo marca Kia, modelo Opirus, Placa XYZ-123, el cual haba estaba reportado como robado.'}
                avatarTitle={avatarTitle}
                hasDivider={hasDivider} />
        );
    });
}

LastMessagesList.propTypes = {
    lastMessages: PropTypes.array.isRequired,
};
